$background-color: #212020;
$font-stack: "Bahnschrift", sans-serif;

@import './title';
@import './menu';

body{
  background-color: $background-color;
  height:101%;
}

.viewport {
    position:absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
}

.float-child {
    position: relative;
    width: 50%;
    height: 101%;
    float: left;
}